<template>
	<view class="content">
		<view class="fzbox">
			<u-row>
				<u-col span="3" style="text-align: center;">
					<u-avatar src="/static/img/banner.png" size="90"></u-avatar>
				</u-col>
				<u-col span="6">
					<p class="titlebox">{{details.data.deptName}}</p>
					<p class="pbox blue">
						<image src="/static/img/address.png" mode="widthFix" class="bz02"></image>
						{{details.data.orgAddress}}
					</p>
				</u-col>
				<u-col span="3" style="text-align: center;">
					<u-button shape="circle" size="mini" plain @click="zrFunt">转让</u-button>
				</u-col>
			</u-row>
		</view>
		<view>
			<u-tabs-swiper ref="uTabs" :list="list" :current="current" @change="tabsChange"
				swiperWidth="750"></u-tabs-swiper>
		</view>

		<swiper :current="swiperCurrent" @transition="transition" @animationfinish="animationfinish">
			<swiper-item class="swiper-item">
				<view style="margin: 30rpx;">
					<view class="numbox">
						<u-row>
							<u-col span="6" style="text-align: center;">
								<span>{{details.userNum}}</span>
								<p>总人数</p>
							</u-col>
							<u-col span="6" style="text-align: center;border-left: 1px solid #E1E1E2;">
								<span>{{details.aboutNum}}</span>
								<p>开展活动数</p>
							</u-col>
						</u-row>
					</view>
					<u-row>
						<u-col span="12" class="txtbox">
							<p>成立时间：<span>{{details.data.date}}</span></p>
							<p>分队队长：<span>{{details.data.orgDz}}</span></p>
							<p>队长电话：<span>{{details.data.phone}}</span></p>
						</u-col>
						<u-col span="12" class="jjbox">
							<p>分队简介</p>
							<view class="jtxt">
								{{details.data.orgIntroduction}}
							</view>
						</u-col>
					</u-row>
				</view>
			</swiper-item>
			<swiper-item class="swiper-item">
				<u-row style="margin: 0 30rpx;">
					<u-col span="12" style="text-align: center;min-height: 118rpx;padding-top: 30rpx;">
						<u-search shape="round" v-model="params.universalAntiUserMain27" @search="Tosearch"
							@custom="Tosearch"></u-search>
					</u-col>
					<u-col span="6" style="padding: 24rpx 0 18rpx 0;font-size: 32rpx;font-weight: bold;color: #333333;">
						在队队员
					</u-col>
					<u-col span="6" style="text-align: right;padding: 24rpx 0 18rpx 0;">
						<u-icon name="arrow-down-fill"></u-icon>
					</u-col>
				</u-row>
				<view class="list02" v-for="(item,index) in list01" :key="index">
					<u-row>
						<u-col span="2">
							<u-avatar :src="item.userFile" size="90"></u-avatar>
						</u-col>
						<u-col span="3">
							<p class="titlebox">{{item.universalAntiUserMain27}}</p>
							<p class="pbox blue">{{Getname(item.orleFid)}}</p>
						</u-col>
						<u-col span="4" style="text-align: center;">
							<p class="pbox">A：{{item.scoreA}} B：{{item.scoreB}}</p>
						</u-col>
						<u-col span="2">
						<!-- 	<u-icon name="more-dot-fill" @click="viewShow=index"></u-icon> -->
							<span @click="checkTo(item,0)" style="color:#0062E9;">修改职位</span>
						</u-col>
					</u-row>
						<!-- <view class="operation" v-show="viewShow==index">
						<u-row>
							<u-col span="12"
								style="border-bottom: 1px solid #E1E1E2;text-align: center;line-height: 50rpx;">
								<view style="position: relative;">
								<u-icon name="close" color="#ccc" size="14" @click="viewShow=-1" style="position: absolute;top:3rpx;right: 10rpx;"></u-icon>
								</view>
							</u-col>
						<u-col span="12"
								style="border-bottom: 1px solid #E1E1E2;text-align: center;line-height: 50rpx;">
								<span @click="checkTo(item,1)">资料编辑</span>
							</u-col> 
							<u-col span="12" style="text-align: center;line-height: 50rpx;">
								<span @click="checkTo(item,2)">队员删除</span>
							</u-col>
						</u-row>
					</view>-->
				</view>
			</swiper-item>
		</swiper>
		<u-modal v-model="modalshow">
					<view class="slot-content">
						
					</view>
				</u-modal>
		<u-select v-model="sushe_show" mode="single-column" :list="susheArr" @confirm="sushe_confirm"
			value-name='dictValue' label-name='dictLabel'></u-select>
	</view>
</template>

<script>
	import {
		antiOrg,
		antiOrgPeople
	} from '../../api/organization.js'
	import {
		getDicts,
		updateObj
	} from '../../api/index.js'
	export default {
		data() {
			return {
				modalshow:false,
				nowItem: {},
				susheArr: [],
				sushe_show: false,
				viewShow: -1,
				params: {
					orgFid: null,
					universalAntiUserMain27: null
				},
				details: {},
				search: 1,
				keyword: '',
				show: 1,
				form: {},
				current: 0,
				swiperCurrent: 0,
				list: [{
					name: '简介'
				}, {
					name: '队员'
				}],
				list01: [{
					name: '可疑物品',
					time: '3天前',
					pic: '/static/img/banner.png'
				}, {
					name: '反恐防恐小常识：如何识别恐怖袭击嫌疑人？',
					time: '3天前',
					pic: '/static/img/banner.png'
				}, {
					name: '反恐',
					time: '3天前',
					pic: '/static/img/banner.png'
				}, ],
			}
		},
		onLoad() {
			this.params.orgFid = this.$route.query.id;
			getDicts('volunteer_team_identity').then(res => {
				if (res.code == 200) {
					this.susheArr = res.data;
				}
			})
			this.listF();
		},
		methods: {
			checkTo(item, no) {
				this.nowItem = item;
				if (no == 0) {
					this.sushe_show = true;
				}else if (no == 2) {
					this.sushe_show = true;
					
				}
			},
			sushe_confirm(e) {
				let queryJson = [{
					data1: 'id',
					data2: '0',
					data3: this.nowItem.id,
					objectId: "294",
					subType: null,
					flag: false,
					fileType: "String"
				},{
					data1: 'orleFid',
					data2: '0',
					data3: e[0].value,
					objectId: "294",
					subType: null,
					flag: false,
					fileType: "String"
				}];
				queryJson = JSON.stringify(queryJson);
				updateObj({
					'queryJson': queryJson
				}).then(res => {
					if (res.code == 200) {
						this.$refs.uToast.show({
							title: '修改成功'
						});
						this.tabsChange(1);
					}
				});
			},
			Getname(val) {

				let result = null;
				this.susheArr.map((item) => {
					if (item.dictValue == val) {
						result = item.dictLabel;
					}
				})
				return result;
			},
			zrFunt() {
				uni.navigateTo({
					url: 'transfer?id=' + this.params.orgFid + '&deptName='+ this.details.data.deptName
				})
			},
			listF() {
				antiOrg(this.params).then(res => {
					if (res.code == 200) {
						this.details = res.data;
					}
				})
			},
			tabsChange(index) {
				this.swiperCurrent = index;
				if (index == 1) {
					antiOrgPeople(this.params).then(res => {
						if (res.code == 200) {
							this.list01 = res.data;
						}
					})
				}
			},
			transition(e) {
				let dx = e.detail.dx;
				this.$refs.uTabs.setDx(dx);
			},
			animationfinish(e) {
				let current = e.detail.current;
				this.$refs.uTabs.setFinishCurrent(current);
				this.swiperCurrent = current;
				this.current = current;
			},
		}
	}
</script>

<style scoped>
	.content {
		width: 100vw;
		height: 100vh;
		background: #F5F6FA;
	}

	.content /deep/ uni-swiper {
		height: 820rpx;
		padding-bottom: 40rpx;
	}

	.content /deep/ .grid-text {
		font-size: 24rpx;
		font-weight: bold;
		color: #333333;
		margin-top: 16rpx;
	}

	.txtbox {
		margin: 50rpx 0 43rpx 0;
	}

	.txtbox p {
		margin-bottom: 10rpx;
		color: #999;
	}

	.txtbox span {
		color: #333;
	}

	.jjbox p {
		margin-bottom: 16rpx;
	}

	.jjbox .jtxt {
		background-color: #fff;
		height: 237rpx;
		padding: 20rpx 18rpx;
		color: #B1B1B1;
		border-radius: 4rpx;
	}

	.numbox {
		width: 100%;
		background-color: #fff;
		height: 138rpx;
		box-shadow: 0 2rpx 8rpx 1rpx rgba(0, 0, 0, 0.06);
		border-radius: 10rpx;
		padding-top: 25rpx;
	}

	.numbox span {
		font-size: 38rpx;
		font-weight: bold;
		color: #296DCB;
	}

	.numbox p {
		font-size: 24rpx;
		color: #333333;
		margin-top: 8rpx;
	}

	.fzbox {
		width: 100%;
		height: 175rpx;
		background: url('@/static/img/fdbg.png') no-repeat center center;
		background-size: cover;
		box-sizing: border-box;
		padding: 38rpx 0;
		color: #fff;
	}

	.blueline {
		position: absolute;
		left: 0;
		top: 24rpx;
		width: 6rpx;
		height: 28rpx;
		background: #0062E9;
	}

	.list02 {
		width: 100%;
		height: 130rpx;
		background: #FFFFFF;
		border-bottom: 1px solid #EEEEEE;
		position: relative;
		font-size: 24rpx;
		font-weight: 400;
		color: #333333;
		padding: 18rpx 10rpx 20rpx 30rpx;
	}

	.list02 .operation {
		position: absolute;
		width: 160rpx;
		height: auto;
		background-color: #fff;
		border-radius: 10rpx;
		top: 80rpx;
		right: 30rpx;
		box-shadow: 0px 2px 8px 1px rgba(0, 0, 0, 0.06);
		z-index: 999;
	}

	.list02 .titlebox {
		font-size: 28rpx;
		font-weight: bold;
		color: #333333;
		width: 98%;
		/*宽度记得设置，这样超出宽度的内容才会被省略*/
		overflow: hidden;
		/*文本超出隐藏*/
		white-space: nowrap;
		text-overflow: ellipsis;
	}

	.list02 .pbox {
		margin-top: 8rpx;
		font-size: 24rpx;
		color: #999999;
		width: 98%;
		/*宽度记得设置，这样超出宽度的内容才会被省略*/
		overflow: hidden;
		/*文本超出隐藏*/
		white-space: nowrap;
		text-overflow: ellipsis;
		/*文本超出省略*/
	}

	.list02 .blue {
		color: #4490FB;
	}

	.list02 b {
		font-size: 24rpx;
		color: #296DCB;
		font-weight: normal;
	}

	.tiptxt {
		position: absolute;
		right: 32rpx;
		top: 20rpx;
	}

	.bz01 {
		width: 52rpx;
		vertical-align: middle;
		display: inline-block;
		margin-left: -10rpx;
	}

	.bz02 {
		width: 28rpx;
		margin-right: 6rpx;
		vertical-align: middle;
		display: inline-block;
	}
</style>